import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {Link, useParams} from "react-router-dom";
import HeaderNavBar from "../../components/headerNavBar/headerNavBar";
import {createUseStyles} from "react-jss";
import arrowUp from '../../img/arrow_up=24.png'
import arrowDown from '../../img/arrow_down=24.png'
import arrow from "../../img/arrowright.svg";
import share from '../../img/share.svg';
import message from '../../img/message.svg';
import Facebook from "../../img/Facebook.svg";
import TikTok from "../../img/TikTok.svg";
import YouTube from "../../img/YouTube.svg";
import Instagram from "../../img/Instagram.svg";
import WhatsApp from "../../img/WhatsApp.svg";
import Telegram from "../../img/Telegram.svg";
import Viber from "../../img/Viber.svg";
import LinkedIn from "../../img/LinkedIn.png";

const useStyles = createUseStyles({
    button: {
        backgroundColor: "rgba(255,255,255,0.7)",
        color: "#5CA91A",
        fontWeight:'700',
        position: "fixed",
        zIndex: 9,
        fontSize:'15px',
        alignSelf:'center',
        bottom: 10,
        border:'2.5px solid #5CA91A',
        padding: "10px 40px",
        borderRadius: "8px",
        cursor: "pointer",
        "&:hover": {
            backgroundColor: "rgba(255,255,255,0.9)"
        }
    },
    overlay: {
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "rgba(0, 0, 0, 0.5)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        zIndex: 9999
    },
    modal: {
        paddingLeft:'10px',
        backgroundColor: '#f5f5f5',
        width: "100%",
        height: "100%",
        overflow: "auto"
    },
    OneCategory2Item: {
        display: "flex",
        flexDirection: "row",
        width:'90vw',
        justifyContent: "space-between",
        padding: "8px 0",
        margin:"0 10px 20px 10px",
        borderBottom: "#DDDDDD solid 1px",
        textDecoration: "none",
        color: "#000000",
    },
    OneCategoryInfo: {
        display: "flex",
        flexDirection: "row",
    },
});




const UserPage = (props, {link}) => {
    const {UserPage} = useParams();
    const [user, setUser] = useState([]);
    const {Categories2} = useParams();
    const {Categories3} = useParams();
    const {SortedCategories} = useParams();
    const [showDescription, setShowDescription] = useState(false);
    const [showWorkTime, setShowWorkTime] = useState(false);
    const [modalOpen, setModalOpen] = useState(false);
    const classes = useStyles();
    const [isCopied, setIsCopied] = useState(false);

    const copyLinkToClipboard = () => {
        const currentUrl = window.location.href;
        navigator.clipboard.writeText(currentUrl);
        setIsCopied(true);
        setTimeout(() => setIsCopied(false), 1000);
    };

    const handleOpenModal = () => {
        setModalOpen(true);
    };

    const handleCloseModal = () => {
        setModalOpen(false);
    };

    const toggleDescription = () => {
        setShowDescription(!showDescription);
    };
    const toggleWorkTime = () => {
        setShowWorkTime(!showWorkTime);
    };
    useEffect(() => {
        const fetchUserProfile = async (userId) => {
            try{
                const {data} = await axios.get(`https://maticht-cdbk1.onrender.com/user-profile/${UserPage}`);
                setUser(data.profile)
                console.log(user)
                console.log('user profile data', data.profile)
            }catch (err){
                console.log(err)
            }
        };
        fetchUserProfile(UserPage);
    }, []);

    function Modal({ children, onClose }) {
        return (
            <div className={classes.overlay}>
                <div className={classes.modal}>
                    <button className={classes.closeButton} onClick={onClose}>
                        X
                    </button>
                    {children}
                </div>
            </div>
        );
    }

    return (
        <div style={{backgroundColor: '#f5f5f5', justifyContent:'center',height:'100%', paddingBottom:'55px'}}>
            <HeaderNavBar />
            <div style={{display:'flex', flexDirection:'row', alignItems:'center', justifyContent:'space-between', margin:'5px 10px 5px 0'}}>
                <Link style={{textDecoration: "none", color: "#454545", fontSize: "14px"}} to={`/AllCategories/${Categories2}/${Categories3}/${SortedCategories}`}>
                    <p style={{textDecoration: "none", color: "#454545", fontSize: "14px", marginLeft:'10px'}}>
                        {`< Список специалистов`}
                    </p>
                </Link>
                <div style={{display:'flex', flexDirection:'row', alignItems:'center',}}>
                    <p style={{display:'flex', alignSelf:'center', margin:'0 5px 3px 0', fontSize:'12px', color:'#666', opacity: isCopied ? "1" : "0",
                        transition: "opacity 0.3s ease-in-out",}}>{isCopied ? "Скопировано!" : null}</p>
                    <img style={{marginRight:'15px'}} onClick={copyLinkToClipboard} src={share} alt={'share'}/>
                    <a href={`mailto:${user.email}`}>
                        <img src={message} alt={'message'}/>
                    </a>
                </div>
            </div>

            <div style={{marginLeft:'10px', width:"94vw", flexDirection:'column', backgroundColor:"#fff", display:"flex", alignSelf:"center", justifyContent:"center", alignItems:'flex-start', borderRadius:8}}>
                {user.image ==='url' || !user.image ? null :
                    <div style={{width:'310px', height:'310px', marginTop:'15px', alignSelf:'center', justifyContent:'center', position:'relative', borderRadius:8, overflow: 'hidden'}}>
                        {user.image && <img style={{width:'100%', height:'100%', filter:'blur(7px)', position: 'absolute'}} src={user.image} alt='User Image' />}
                        {user.image && <img style={{width:'100%', height:'100%', objectFit:'contain', position:'absolute', top:0, left:0}} src={user.image} alt='User Image' />}
                    </div>
                }
                <div style={{paddingLeft:'10px', marginBottom:'15px'}}>
                    <h2 style={{margin:'5px 0'}}>{user.firstName} {user.lastName}</h2>
                    <h6 style={{margin:'8px 0', color:'#666', display: user.nameOrCompany ==='Name Or Company' ? 'none' : null }}>{user.nameOrCompany}</h6>
                    <h5 style={{margin:'5px 0', display: user.areasActivity ==='Areas Activity' ? 'none' : 'flex'}}>{user.areasActivity }({user.services})</h5>
                    <p style={{ wordWrap: "break-word", textAlign: "left",width:'90vw', color:'#666',margin:'5px 0', display: user.description ==='Description' ? 'none' : null }}>
                        {user.description ? (
                            <>
                                {showDescription
                                    ? user.description
                                    : `${user.description.slice(0, 40)}${
                                        user.description.length > 40 ? "... " : " "
                                    }`}

                                {user.description.length > 40 && (
                                    <div >
                                    <button
                                    style={{
                                    background: 'none',
                                    border: 'none',
                                    color: '#666666',
                                    textDecoration: 'none',
                                    cursor: 'pointer',
                                    fontSize:'12px',
                                    margin:"0px",
                                    padding:'0px'
                                }}
                                    onClick={toggleDescription}
                                    >
                                        {showDescription ? (
                                            <div style={{display:'flex', alignItems:'center', flexDirection:'row'}}>
                                                <p>Свернуть </p>
                                                <img style={{width:'10px', height:'10px', marginLeft:'5px',marginTop:'3px'}} src={arrowUp} alt={'>'}/>
                                            </div>

                                        ) : (
                                            <div style={{display:'flex', alignItems:'center', flexDirection:'row'}}>
                                                <p>Показать больше </p>
                                                <img style={{width:'10px', height:'10px',marginTop:'3px', marginLeft:'5px'}} src={arrowDown} alt={'>'}/>
                                            </div>
                                        )}
                                    </button>
                                    </div>

                                )}
                            </>
                        ) : (
                            'Описание отсутствует'
                        )}
                    </p>
                    <div style={{display: user.city ==='City' ? 'none' : null}} >
                        <p style={{margin:'5px 0', fontSize:'12px', color:'#666'}}>
                            Адрес
                        </p>
                        <p style={{margin:'5px 0', fontWeight:'400'}}>
                            {user?.street ==='street'? null :`${user.street} `}
                            {user?.house ==='house'? null :`${user.house}`}
                            {user?.apartment ==='apartment'? null :`(${user.apartment}) `}
                            {user?.city ==='city'? null :`${user.city}, `}
                            {user?.region ==='region'? null :`${user.region}, `}
                            {user?.zip ==='zip'? null :`${user.zip}`}
                        </p>
                    </div>
                    <div style={{margin: '15px 0 0 0'}}>
                        <p style={{margin:'5px 0', fontSize:'12px', color:'#666'}}>
                            Время работы
                        </p>
                        <p style={{margin:'5px 0', fontWeight:'400'}}>ПН:{user.workingHoursMon}</p>
                        <p style={{margin:'5px 0', fontWeight:'400', display: showWorkTime ? 'flex' : 'none'}}>ВТ:{user.workingHoursTue === 'workingHours' ? ' - ' : user.workingHoursTue}</p>
                        <p style={{margin:'5px 0', fontWeight:'400', display: showWorkTime ? 'flex' : 'none'}}>СР:{user.workingHoursWed === 'workingHours' ? ' - ' : user.workingHoursWed}</p>
                        <p style={{margin:'5px 0', fontWeight:'400', display: showWorkTime ? 'flex' : 'none'}}>ЧТ:{user.workingHoursThu === 'workingHours' ? ' - ' : user.workingHoursThu}</p>
                        <p style={{margin:'5px 0', fontWeight:'400', display: showWorkTime ? 'flex' : 'none'}}>ПТ:{user.workingHoursFri === 'workingHours' ? ' - ' : user.workingHoursFri}</p>
                        <p style={{margin:'5px 0', fontWeight:'400', display: showWorkTime ? 'flex' : 'none'}}>СБ:{user.workingHoursSat === 'workingHours' ? ' - ' : user.workingHoursSat}</p>
                        <p style={{margin:'5px 0', fontWeight:'400', display: showWorkTime ? 'flex' : 'none'}}>ВС:{user.workingHoursSun === 'workingHours' ? ' - ' : user.workingHoursSun}</p>
                            <div >
                                <button
                                    style={{
                                        background: 'none',
                                        border: 'none',
                                        color: '#666666',
                                        textDecoration: 'none',
                                        cursor: 'pointer',
                                        fontSize:'12px',
                                        margin:"0px",
                                        padding:'0px'
                                    }}
                                    onClick={toggleWorkTime}
                                >
                                    {showWorkTime ? (
                                        <div style={{display:'flex', alignItems:'center', flexDirection:'row'}}>
                                            <p>Свернуть </p>
                                            <img style={{width:'10px', height:'10px', marginLeft:'5px',marginTop:'3px'}} src={arrowUp} alt={'>'}/>
                                        </div>

                                    ) : (
                                        <div style={{display:'flex', alignItems:'center', flexDirection:'row'}}>
                                            <p>Показать больше </p>
                                            <img style={{width:'10px', height:'10px',marginTop:'3px', marginLeft:'5px'}} src={arrowDown} alt={'>'}/>
                                        </div>
                                    )}
                                </button>
                            </div>
                    </div>
                </div>
            </div>
            <div style={{display: user.areasActivity ==='Areas Activity' ? 'none' : null}}>
                <h2 style={{margin:'15px 0 15px 10px'}}>Услуги</h2>
                <div style={{marginLeft:'10px', width:"94vw", flexDirection:'column', backgroundColor:"#fff", display:"flex", alignSelf:"center", justifyContent:"center", alignItems:'flex-start', borderRadius:8}}>
                    <h3 style={{margin:'5px 0 5px 10px'}}>{user.areasActivity}</h3>
                    <div  className={classes.OneCategory2Item}>
                        <div className={classes.OneCategoryInfo}>
                            <p className={'OneCategoryTitle'}>{user.services}</p>
                        </div>
                        <p style={{marginRight:'5px'}} className={'OneCategoryTitle'}>{user.price}</p>
                    </div>
                </div>
            </div>

            <div style={{width:'100vw',display:'flex', justifyContent:'center', marginBottom:"10px"}}>
                <button className={classes.button} onClick={handleOpenModal}>
                    Посмотреть способы связи
                </button>
            </div>


            {modalOpen && (
                <div className={classes.overlay}>
                    <div className={classes.modal}>
                        <p style={{textDecoration: "none", color: "#454545", fontSize: "14px"}} onClick={handleCloseModal}>
                            {`< ${user.firstName} ${user.lastName}`}
                        </p>
                        <h2>Контакты</h2>
                        <div style={{padding:'15px 10px',marginRight:'10px', flexDirection:'column', backgroundColor:"#fff", display:"flex", alignSelf:"center", justifyContent:"center", alignItems:'flex-start', borderRadius:8}}>
                            <div>
                                <p style={{margin:'5px 0', fontSize:'12px', color:'#666'}}>
                                    Почта
                                </p>
                                <p style={{margin:'5px 0', fontWeight:'400'}}>{user.email}</p>
                            </div>
                            <div style={{margin: '15px 0 0 0', display: user.Facebook ==='link' && user.LinkedIn ==='link' && user.Instagram ==='link' && user.Viber ==='link' && user.Telegram ==='link' && user.TikTok ==='link' && user.WhatsApp ==='link' && user.YouTube ==='link' ? 'none' : null}}>
                                <p style={{margin:'5px 0', fontSize:'12px', color:'#666'}}>
                                    Социальные сети
                                </p>
                                <div>
                                    {user.Facebook !== 'link' && (
                                        <a href={user.Facebook}>
                                            <img src={Facebook} width="23px"  height="23px" style={{marginRight:'10px',}} alt='Facebook'/>
                                        </a>
                                    )}
                                    {user.LinkedIn !== 'link' && (
                                        <a href={user.LinkedIn}>
                                            <img src={LinkedIn} width="23px"  height="23px" style={{marginRight:'10px',}} alt='LinkedIn'/>
                                        </a>
                                    )}
                                    {user.Instagram !== 'link' && (
                                        <a href={user.Instagram}>
                                            <img src={Instagram} width="23px"  height="23px" style={{marginRight:'10px',}} alt='Instagram'/>
                                        </a>
                                    )}
                                    {user.Viber !== 'link' && (
                                        <a href={user.Viber}>
                                            <img src={Viber} width="23px"  height="23px" style={{marginRight:'10px',}} alt='Viber'/>
                                        </a>
                                    )}
                                    {user.Telegram !== 'link' && (
                                        <a href={user.Telegram}>
                                            <img src={Telegram} width="23px"  height="23px" style={{marginRight:'10px',}} alt='Telegram'/>
                                        </a>
                                    )}
                                    {user.TikTok !== 'link' && (
                                        <a href={user.TikTok}>
                                            <img src={TikTok} width="23px"  height="23px" style={{marginRight:'10px',}} alt='TikTok'/>
                                        </a>
                                    )}
                                    {user.WhatsApp !== 'link' && (
                                        <a href={user.WhatsApp}>
                                            <img src={WhatsApp} width="23px"  height="23px" style={{marginRight:'10px',}} alt='WhatsApp'/>
                                        </a>
                                    )}
                                    {user.YouTube !== 'link' && (
                                        <a href={user.YouTube}>
                                            <img src={YouTube} width="23px"  height="23px" style={{marginRight:'10px',}} alt='YouTube'/>
                                        </a>
                                    )}
                                </div>
                            </div>
                            <div style={{margin: '15px 0 0 0', display: user.phone1 ==='phone1' && user.phone2 ==='phone2' ? 'none' : null}}>
                                <p style={{margin:'5px 0', fontSize:'12px', color:'#666'}}>
                                    Номер телефона
                                </p>
                                {user.phone1 !== 'phone1' && (
                                    <p style={{margin:'5px 0', fontWeight:'400'}}>{user.phone1}</p>
                                )}
                                {user.phone2 !== 'phone2' && (
                                    <p style={{margin:'5px 0', fontWeight:'400'}}>{user.phone2}</p>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default UserPage;
