import './сategories.css';
import CategoriesJSON from '../../data/categories.json';
import {Link} from "react-router-dom";
import beauty from '../../img/CategoriesLogo/Красота и уход.svg';
import sport from '../../img/CategoriesLogo/RoundIcons_FreeSet-08.svg';
import health from '../../img/CategoriesLogo/Здоровье и спорт.svg';
import auto from '../../img/CategoriesLogo/Австоуслуги.svg'
import finance from '../../img/CategoriesLogo/Finance.svg'
import animals from '../../img/CategoriesLogo/Животные.svg';
import photo from '../../img/CategoriesLogo/Фото, видео, аудио.svg';
import study from '../../img/CategoriesLogo/Обучениеирепетиторство.svg';
import ads from '../../img/CategoriesLogo/Реклама, СММ и маркетинг.svg';
import design from '../../img/CategoriesLogo/Дизайн и пректирование.svg';
import programming from '../../img/CategoriesLogo/Разработка и программирование.svg';
import logistics from '../../img/CategoriesLogo/Транспорт и логистика.svg';
import house from '../../img/CategoriesLogo/Уборки и помощь по дому.svg';
import build from '../../img/CategoriesLogo/Ремонт и стройка.svg';
import party from '../../img/CategoriesLogo/Развлечения.svg';


function Categories() {
    const categoryList = CategoriesJSON.categories.map((category) =>
        <Link  className={'categoryItem'} to={`/AllCategories/${category.categoriestitle}`}>
            <img className={'categoryImg'} src={
                (category.categoriestitle === "Красота и уход") ?
                beauty : (category.categoriestitle === "Спорт") ?
                sport : (category.categoriestitle === "Здоровье") ?
                health : (category.categoriestitle === "Авто") ?
                auto : (category.categoriestitle === "Финансы и законы") ?
                finance : (category.categoriestitle === "Животные") ?
                animals : (category.categoriestitle === "Образование") ?
                study : (category.categoriestitle === "Фото, видео, аудио") ?
                photo : (category.categoriestitle === "Продвижение и реклама") ?
                ads : (category.categoriestitle === "Дизайн и проектирование") ?
                design : (category.categoriestitle === "Разработка") ?
                programming : (category.categoriestitle === "Транспорт и логистика") ?
                logistics : (category.categoriestitle === "Помощь по дому") ?
                house : (category.categoriestitle === "Строительство и ремонт") ?
                build : (category.categoriestitle === "Развлечения и мероприятия") ?
                party : category.imgId
            } alt="image not found"/>
            <p className={'categoryTitle'}>{category.categoriestitle}</p>
        </Link>
    );

    return (
        <div>
            <nav className={'categoriesNav'} >
                <h2>Категории</h2>
                <Link className={'allCategoriesBtn'} to="/AllCategories">Все категории</Link>
            </nav>
            <div className={'categories'}>
                <div className={'categoriesBlock'}>
                    {categoryList}
                </div>
            </div>
        </div>
    );
}

export default Categories;


