import logo from '../../img/mainPageLogo.svg';
import './headerNavBar.css';
import {Link} from "react-router-dom";
import React, {useState} from "react";
import ModalUserPage from "../../components/modalUserPage/modalUserPage";
import noneAccBtn from '../../img/Frame.svg'

function HeaderNavBar() {
    const [modalOpen, setModalOpen] = useState(false);
    const user = localStorage.getItem("token");
    console.log(user);
    let localUserObj = JSON.parse(user);

    const handleOpenModal = () => {
        setModalOpen(true);
    };

    const handleCloseModal = () => {
        setModalOpen(false);
    };


    return (
        <div className={'navBarBlock'}>
            <Link to="/">
                <img src={logo} className="App-logo" alt="logo" />
            </Link>
            <div>
                <ModalUserPage isOpen={modalOpen} onClose={handleCloseModal} />
            </div>
            {!modalOpen ? (
                <div>
                    {user ? (
                        <div>
                            <a onClick={handleOpenModal} className="logOutBlock">
                                <p className="accountText">
                                    {`${localUserObj.data?.firstName} ${localUserObj.data?.lastName}`}
                                </p>
                                {localUserObj.data.image === 'url' || !localUserObj.data.image ?
                                    <img src={noneAccBtn} style={{width: '24px', height: '24px', margin: '0 1vw', opacity: '0.55'}} alt={'userLogo'}/> :
                                    <img src={localUserObj.data?.image} style={{width: '24px', height: '24px', margin: '0 1vw', borderRadius:'15px'}} alt={'userLogo'}/>
                                }
                            </a>
                        </div>
                    ) : (
                        <Link className="logInBlock" to={"/login"}>
                            <p className="accountText">Войти</p>
                            <button className="accountBtn"></button>
                        </Link>
                    )}
                </div>
            ) : null}

        </div>
    );
}

export default HeaderNavBar;
