import React, { useState } from "react";
import axios from "axios";
import Modal from "react-modal";
import {Link, useNavigate, useParams} from "react-router-dom";
import styles from "./styles.module.css";
import {createUseStyles} from "react-jss";
import CategoriesJSON from '../../data/categories.json';
import CitiesJSON from '../../data/cities.json';
import arrow from '../../img/arrowright.svg';
import beauty from '../../img/CategoriesLogo/Красота и уход.svg';
import sport from '../../img/CategoriesLogo/RoundIcons_FreeSet-08.svg';
import health from '../../img/CategoriesLogo/Здоровье и спорт.svg';
import auto from '../../img/CategoriesLogo/Австоуслуги.svg'
import finance from '../../img/CategoriesLogo/Finance.svg'
import animals from '../../img/CategoriesLogo/Животные.svg';
import photo from '../../img/CategoriesLogo/Фото, видео, аудио.svg';
import study from '../../img/CategoriesLogo/Обучениеирепетиторство.svg';
import ads from '../../img/CategoriesLogo/Реклама, СММ и маркетинг.svg';
import design from '../../img/CategoriesLogo/Дизайн и пректирование.svg';
import programming from '../../img/CategoriesLogo/Разработка и программирование.svg';
import logistics from '../../img/CategoriesLogo/Транспорт и логистика.svg';
import house from '../../img/CategoriesLogo/Уборки и помощь по дому.svg';
import build from '../../img/CategoriesLogo/Ремонт и стройка.svg';
import party from '../../img/CategoriesLogo/Развлечения.svg';
import arrowDown from '../../img/arrow_down=24.png';

const useStyles = createUseStyles({
    overlay: {
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "rgba(0, 0, 0, 0.5)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        zIndex: 9999
    },
    modal: {
        paddingLeft:'10px',
        backgroundColor: '#f5f5f5',
        width: "100%",
        height: "100%",
        overflow: "auto"
    },
    container: {
        minHeight: "100vh",
        backgroundColor: "#F1F1F1"
    },
    allCategoriesBtn: {
        textDecoration: "none",
        color: "#454545",
        fontSize: "14px",
    },
    AllCategoryContainer: {
        margin: "0 2vw",
        padding: "15px 0 40px 0",
    },
    OneCategoryInfo: {
        display: "flex",
        textDecoration:'none',
        flexDirection: "row",
    },
    OneCategoryImg: {
        width: "60px",
        marginRight: "10px",
    },
    OneCategoryItem: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        padding: "10px 0",
        borderBottom: "#DDDDDD solid 1px",
        textDecoration: "none",
        color: "#000000",

    },
    OneCategoryCity: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        textDecoration: "none",
        color: "#000000",

    },
    OneCategoryCheckItem: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        padding: "15px 0",
        borderBottom: "#DDDDDD solid 1px",
        textDecoration: "none",
        color: "#000000",
        marginRight:'10px'
    }

});


const AddWorkingHours = () => {
    const {UserPage} = useParams();
    const [data, setData] = useState({
        firstName: "",
        lastName: "",
        password: "",
        nameOrCompany: "",
        areasActivity:  "",
        phone1: "",
        phone2: "",
        image: "",
        workingHoursMon: "",
        workingHoursTue: "",
        workingHoursWed: "",
        workingHoursThu: "",
        workingHoursFri: "",
        workingHoursSat: "",
        workingHoursSun: "",
        Facebook: "",
        TikTok: "",
        YouTube: "",
        Instagram: "",
        WhatsApp: "",
        Telegram: "",
        Viber: "",
        LinkedIn: "",
        city: "",
        region: "",
        street: "",
        house: "",
        apartment: "",
        zip: "",
        workLocation:'',
        description: "",
        services: "",
        price: "",
        additionalImage1: "",
        additionalImage2: "",
        additionalImage3: "",
        additionalImage4: "",
        additionalImage5: "",
        additionalImage6: "",
        additionalImage7: "",
        additionalImage8: "",
        additionalImage9: "",
        additionalImage10: "",
    });
    const [error, setError] = useState("");
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [modalServIsOpen, setModalServIsOpen] = useState(false);
    const [modalCitiesIsOpen, setModalCitiesIsOpen] = useState(false);
    const [selectedOption, setSelectedOption] = useState("");
    const [selectedServ, setSelectedServ] = useState("");
    const [selectedCities, setSelectedCities] = useState("");

    const [selectedCategoryArray, setSelectedCategoryArray] = useState([]);

    // Получение доступа к объекту CategoriesJSON.categories
    const categories = CategoriesJSON.categories;
    //
    // // Поиск элемента с выбранным значением selectedOption по параметру categoriesTitle
    // const selectedCategory = categories.find((category) => category.categoriestitle === selectedOption);
    //
    // // Создание нового массива, содержащего найденный элемент
    // const newSelectedCategoryArray = [selectedCategory];
    //
    // // Обновление состояния с помощью setSelectedCategoryArray
    // setSelectedCategoryArray(newSelectedCategoryArray);
    const classes = useStyles();


    const navigate = useNavigate();
    const handleChange = ({ currentTarget: input }) => {
        setData({ ...data, [input.name]: input.value });
    };


    const handleSubmit = async (e) => {
        e.preventDefault();
        console.log(data)
        try {
            const url = `https://maticht-cdbk1.onrender.com/update/${UserPage}`;
            const { data: res } = await axios.put(url, data);
            localStorage.setItem("token",  JSON.stringify(res));
            navigate(`/AddDescription/${UserPage}`);
            console.log(data);
        } catch (error) {
            if (
                error.response &&
                error.response.status >= 400 &&
                error.response.status <= 500
            ) {
                setError(error.response.data.message);
            }
        }
    };
    const [modalOpen, setModalOpen] = useState(false);
    const [startTime, setStartTime] = useState('08:00');
    const [endTime, setEndTime] = useState('17:00');

    function handleStartTimeChange(event) {
        setStartTime(event.target.value);
    }

    function handleEndTimeChange(event) {
        setEndTime(event.target.value);
    }
    const [selectedServices, setSelectedServices] = useState([]);

    const handleServiceCheckboxChange = (event, optionTitle) => {
        if (event.target.checked) {
            setSelectedServices([...selectedServices, optionTitle]);
        } else {
            setSelectedServices(selectedServices.filter((title) => title !== optionTitle));
        }
    };

    const handleServicesConfirmation = () => {
        setData({ ...data, services: selectedServices.join(", ") });
        setSelectedServ(selectedServices.join(", "));
        setModalServIsOpen(false);
    };
    const handleCitiesClick = (city) => {
        setSelectedCities(city);
        setData({ ...data, city: city });
        setModalCitiesIsOpen(false);
    };


    const handleOpenModal = (e) => {
        e.preventDefault();
        setModalIsOpen(true);
    };

    const handleOpenServModal = (e) => {
        e.preventDefault();
        setModalServIsOpen(true);
    };
    const handleOpenCitiesModal = (e) => {
        e.preventDefault();
        setModalCitiesIsOpen(true);
    };
    function handleTimeInputKeyDown(event) {
        const input = event.target;
        const key = event.key;
        const position = input.selectionStart;

        if (key === ":" && position === 2) {
            input.value = input.value.substring(0, 2) + ":" + input.value.substring(2);
            input.selectionStart = 3;
            input.selectionEnd = 3;
            event.preventDefault();
        } else if (key === "-" && position === 5) {
            input.value = input.value.substring(0, 5) + "-" + input.value.substring(5);
            input.selectionStart = 6;
            input.selectionEnd = 6;
            event.preventDefault();
        } else if (key === ":" && position === 8) {
            input.value = input.value.substring(0, 8) + ":" + input.value.substring(8);
            input.selectionStart = 9;
            input.selectionEnd = 9;
            event.preventDefault();
        }
    }
    const buttonStyles = selectedOption.length === 0 ? { opacity: 0.5, pointerEvents: 'none' } : {};



    return (
        <div className={styles.signup_container}>
            <div style={{ width: "94%", height: "3px", background: "#fff", borderRadius:'5px', margin:'10px', alignSelf:'center' }}>
                <div style={{ width: `71.42%`, height: "3px", background: "#5CA91A", borderRadius:'5px' }}></div>
            </div>
            <Link style={{textDecoration: "none", color: "#454545", fontSize: "14px"}} to={`/AddSocialInfo/${UserPage}`}>
                <p style={{textDecoration: "none", color: "#454545", fontSize: "14px", marginLeft:'10px'}}>
                    {`< Назад`}
                </p>
            </Link>
            <form className={styles.form_container} onSubmit={handleSubmit} noValidate>
                <h1 style={{margin:"0 0 0 10px"}}>Время работы</h1>
                <div style={{justifyContent:"flex-start", backgroundColor:"#fff",borderRadius:8, margin:"10px 10px", padding:"20px 10px"}}>
                    <div>
                        <input
                            type="text"
                            placeholder="Понедельник"
                            name="workingHoursMon"
                            onChange={handleChange}
                            value={data.workingHoursMon}
                            required
                            className={styles.input}
                        />
                        <input
                            type="text"
                            placeholder="Вторник"
                            name="workingHoursTue"
                            onChange={handleChange}
                            value={data.workingHoursTue}
                            required
                            className={styles.input}
                        />
                        <input
                            type="text"
                            placeholder="Среда"
                            name="workingHoursWed"
                            onChange={handleChange}
                            value={data.workingHoursWed}
                            required
                            className={styles.input}
                        />
                        <input
                            type="text"
                            placeholder="Четверг"
                            name="workingHoursThu"
                            onChange={handleChange}
                            value={data.workingHoursThu}
                            required
                            className={styles.input}
                        />
                        <input
                            type="text"
                            placeholder="Пятница"
                            name="workingHoursFri"
                            onChange={handleChange}
                            value={data.workingHoursFri}
                            required
                            className={styles.input}
                        />
                        <input
                            type="text"
                            placeholder="Суббота"
                            name="workingHoursSat"
                            onChange={handleChange}
                            value={data.workingHoursSat}
                            required
                            className={styles.input}
                        />
                        <input
                            type="text"
                            placeholder="Воскресенье"
                            name="workingHoursSun"
                            onChange={handleChange}
                            value={data.workingHoursSun}
                            required
                            className={styles.input}
                        />
                    </div>
                </div>
                {error && <div className={styles.error_msg}>{error}</div>}
                <button type="submit" className={styles.green_btn}>
                    Изменить
                </button>
            </form>
        </div>
    );
};

export default AddWorkingHours;
