import React, {useEffect, useState} from 'react';
import { createUseStyles } from "react-jss";
import ModalUserPage from "../../components/modalUserPage/modalUserPage";
import axios from "axios";
import {Link, useParams} from "react-router-dom";

const useStyles = createUseStyles({
    container: {
        minHeight: "100vh",
        backgroundColor: "#F1F1F1"
    },
    allCategoriesBtn: {
        textDecoration: "none",
        color: "#454545",
        fontSize: "14px",
    },
    AllCategoryContainer: {
        margin: "0 2vw",
        padding: "15px 0 40px 0",
    },
    OneCategoryInfo: {
        display: "flex",
        flexDirection: "row",
    },
    OneCategoryImg: {
        width: "60px",
        marginRight: "10px",
    },
    OneFavoriteInfo: {
        width:"95vw",
        height:"50px",
        backgroundColor:"#fff",
        marginBottom:10,
        display:"flex",
        paddingLeft:'15px',
        justifyContent:"center",
        alignItems:'center',
        borderRadius:8
    }
});

const FavoritesPage = () => {
    const [modalOpen, setModalOpen] = useState(false);
    const [users, setUsers] = useState([]);
    const classes = useStyles();
    const {Categories2} = useParams();
    const {Categories3} = useParams();
    const {SortedCategories} = useParams();

    useEffect(() => {
        const fetchUsers = async () => {
            const response = await axios.get('https://maticht-cdbk1.onrender.com/getAllUsers');
            setUsers(response.data);
            localStorage.getItem("token");
        };

        fetchUsers();
    }, []);


    const handleOpenModal = () => {
        setModalOpen(true);
    };

    const handleCloseModal = () => {
        setModalOpen(false);
    };
    return (
        <div style={{marginTop:'-15px', padding:'10px', minHeight: "100vh",
            backgroundColor: "#F1F1F1"}}>
            <a style={{textDecoration: "none", color: "#454545", fontSize: "14px"}} onClick={handleOpenModal}>
                <p style={{textDecoration: "none", color: "#454545", fontSize: "14px"}}>
                    {`< Назад`}
                </p>
            </a>
            <div>
                <ModalUserPage isOpen={modalOpen} onClose={handleCloseModal} />
            </div>
            <h1>Избранное</h1>
            <h6>{`В избранных ${users.length} специалистов`}</h6>
            <div style={{display:"flex",flexDirection:"column", justifyContent:"center"}}>
                {users.map((user) => (
                    <div style={{width:"92vw", height:"50px", backgroundColor:"#fff", marginBottom:10, display:"flex", alignSelf:"center", justifyContent:"flex-start", alignItems:'center', borderRadius:8, paddingLeft:'10px'}} key={user.id}>
                        <Link style={{textDecoration: "none", color:"#000"}} to={`/AllCategories/${Categories2}/${Categories3}/${SortedCategories}/${user._id}`}>{user.firstName} {user.lastName}</Link>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default FavoritesPage;
