import React, { useState } from "react";
import axios from "axios";
import {Link, useNavigate, useParams} from "react-router-dom";
import styles from "./styles.module.css";
import AsyncStorage from "@react-native-async-storage/async-storage";
import ModalUserPage from "../modalUserPage/modalUserPage";
import plus from "../../img/Plus.svg";
import Resizer from "react-image-file-resizer";


const UpdateImage = () => {
    const {UserPage} = useParams();
    const [data, setData] = useState({
        email: "",
        firstName: "",
        lastName: "",
        password: "",
        nameOrCompany: "",
        areasActivity:  "",
        city: "",
        region: "",
        detailedAddress: {
            street: "",
            house: "",
            apartment: "",
            zip: ""
        },
        phone:  {
            phone1: "",
            phone2: "",
        },
        workingHours: {
            Mon: "",
            Tue: "",
            Wed: "",
            Thu: "",
            Fri: "",
            Sat: "",
            Sun: "",
        },
        social: {
            Facebook: "",
            TikTok: "",
            YouTube: "",
            Instagram: "",
            WhatsApp: "",
            Telegram: "",
            Viber: "",
            LinkedIn: "",
        },
        description: "",
        services: "",
        price: "",
        image: "",
        additionalImages: {
            image1: "",
            image2: "",
            image3: "",
            image4: "",
            image5: "",
            image6: "",
            image7: "",
            image8: "",
            image9: "",
            image10: "",
        },
    });
    const [error, setError] = useState("");
    const navigate = useNavigate();
    const [selectedImage, setSelectedImage] = useState("");
    const [errorMessage, setErrorMessage] = useState("");

    const handleImageChange = (event) => {
        const selectedFile = event.target.files[0];


        if (selectedFile.size > 5 * 1024 * 1024) {
            setErrorMessage("Размер файла не может превышать 5Mb");

        } else {
            setErrorMessage("");
            setData({ ...data, image: event.target.files[0] });
            const reader = new FileReader();
            Resizer.imageFileResizer(
                selectedFile,
                300,
                300,
                "JPEG",
                100,
                0,
                (uri) => {
                    setSelectedImage(uri);
                    setData({ ...data, image: event.target.files[0] });
                },
                "base64",
                200,
                200
            );
            reader.readAsDataURL(selectedFile);
            reader.onload = () => {
                setSelectedImage(reader.result);
            };
        }
    };
    // const handleChange = ({ currentTarget: input }) => {
    //     setData({ ...data, [input.name]: input.value });
    //     const reader = new FileReader();
    //     reader.readAsDataURL(selectedFile);
    //     reader.onload = () => {
    //         setSelectedImage(reader.result);
    //     };
    // };

    const [image, setImage] = useState(null);

    // const handleImageChange = (e) => {
    //     setData({ ...data, image: e.target.files[0] });
    //     console.log(e.target.files)
    // };


    const handleUpload = async () => {
        try {
            const url = `https://maticht-cdbk1.onrender.com/uploadPhoto/${UserPage}`;
            const formData = new FormData();
            formData.append("image", data.image);
            const res = await axios.put(url, formData, {
                headers: {
                    "Content-Type": "multipart/form-data",
                },
            });
            setData({ ...data, image: res.data }); // update image URL in data state
            localStorage.setItem("token", JSON.stringify(res));
            navigate("/EditProfile");
            console.log(data);
        } catch (err) {
            console.error(err);
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        console.log(data)
        try {

            const url = `${process.env.SERVER_URI}/update/${UserPage}`;
            const { data: res } = await axios.put(url, data);
            localStorage.setItem("token",  JSON.stringify(res));
            navigate("/");
            console.log(data);
        } catch (error) {
            if (
                error.response &&
                error.response.status >= 400 &&
                error.response.status <= 500
            ) {
                setError(error.response.data.message);
            }
        }
    };
    const [modalOpen, setModalOpen] = useState(false);


    const handleOpenModal = () => {
        setModalOpen(true);
    };

    const handleCloseModal = () => {
        setModalOpen(false);
    };

    return (
        <div className={styles.signup_container}>
            <Link style={{textDecoration: "none", color: "#454545", fontSize: "14px"}} to="/EditProfile">
                <p style={{textDecoration: "none", color: "#454545", fontSize: "14px", marginLeft:'10px'}}>
                    {`< Назад`}
                </p>
            </Link>
            <div>
                <ModalUserPage isOpen={modalOpen} onClose={handleCloseModal} />
            </div>
            <div style={{height:'100%', width:'100%'}}>
                <form className={styles.form_container} onSubmit={handleSubmit} noValidate>
                    <h1 style={{margin:"0 0 0 10px"}}>Изображения</h1>
                    <div style={{display: "flex", justifyContent: "space-between", backgroundColor:"#fff", borderRadius:8, margin:"10px", padding:"20px 15px"}}>
                        <div>
                            <label
                                htmlFor="image-upload"
                                style={{
                                    position: "relative",
                                    width: "40vw",
                                    height: "40vw",
                                    borderRadius: "8px",
                                    backgroundColor: "#f1f1f1",
                                    display: "flex",
                                    alignItems: "center",
                                    justifyContent: "center",
                                }}
                            >
                                {selectedImage ? (
                                    <img
                                        src={selectedImage}
                                        alt="Selected"
                                        style={{ width: "100%",borderRadius: "8px", height: "100%" }}
                                    />
                                ) : (
                                    <>
                                        <img src={plus} alt="Plus" />
                                    </>
                                )}
                                <input
                                    id="image-upload"
                                    type="file"
                                    onChange={handleImageChange}
                                    style={{
                                        position: "absolute",
                                        width: "100%",
                                        height: "100%",
                                        top: 0,
                                        left: 0,
                                        opacity: 0,
                                        cursor: "pointer",
                                    }}
                                />
                            </label>
                        </div>
                        <div style={{width: "40vw",
                            height: "40vw",}}>
                            <h5 style={{margin:"0px 0 5px 0", fontSize:'12px'}}>Главное изображение</h5>
                            <p style={{fontSize:'10px', color:'#5B5B5B'}}>Желательно добавить ваше фото или логотип. Рекомендуемое соотношение — 1:1</p>
                        </div>
                    </div>
                    {errorMessage && <div className={styles.error_msg}>{errorMessage}</div>}
                    <button disabled={!data.image || data.image.length > 5242880} type="submit" onClick={handleUpload} className={styles.green_btn}>
                        Изменить
                    </button>
                </form>
            </div>

        </div>
    );
};

export default UpdateImage;
